import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Table,
  Button,
  message,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {hasVariant, getVariant, removeVariant} from 'Variant'
import JsBarcode from 'jsbarcode'
const ORDER_LIST = 'wms/inWarehouseList'

@connect(
  state => ({...state.inWarehouseList}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '序号',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '商品编码',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '商品条码',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '商品名称',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '单价（元）',
      dataIndex: 'price',
      key: 'price',
      align: 'center'
    }, {
      title: '收货单位',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '计划量',
      dataIndex: 'planQty',
      key: 'planQty',
      align: 'center'
    }, {
      title: '已收量',
      dataIndex: 'receiveQty',
      key: 'receiveQty',
      align: 'center'
    }, {
      title: '赠品数',
      dataIndex: 'giftQty',
      key: 'giftQty',
      align: 'center'
    }, {
      title: '金额总计（元）',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }]
  }
  componentDidMount() {
    const {match: {params: {id}}} = this.props
    this._getAsyncData(id)
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData(type) {
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error('订单编号错误'))
      console.log(this.props)
      ids.map((o) => {
        reqData.push({
          id: o
        })
      })
      const {handleGetDetailPrint, update} = this.props
      handleGetDetailPrint({data: reqData}, type)
    } catch (err) {
      console.error(err)
    }
  }
  /* 关闭窗口 */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /* 打印 */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error('更新打印次数失败，订单编号错误'))
      ids.map((o) => {
        reqData.push({
          id: o
        })
      })
      const {handleUpdatePrintNum} = this.props
      handleUpdatePrintNum({data: reqData})
    } catch (err) {
      console.error(err)
    }
  }

  // 画条形码
  componentDidUpdate() {
    const {printData} = this.props
    printData.map((o) => {
      JsBarcode(`#key${o.id}`, o.orderCode, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  render() {
    const {printData, tableLoading, printLoading, match: {params: {id}}} = this.props
    return (
      <React.Fragment>
        {
          printData.length ? <Button className="noprint" type={'primary'} onClick={this.handlePrint}>打印</Button> : (printLoading ? <h2 style={{textAlign: 'center'}}>正在加载打印数据</h2> : <h2 style={{textAlign: 'center'}}>暂无打印数据</h2>)
        }
        {
          printData.map((o, i) => <section className="outboundOrderTemplate PageBreakAfter" key={i}>
              <div style={{position: 'relative', height: '60px', marginBottom: '20px', borderBottom: '1px solid #000', marginTop: '20px'}}>
                <h2 className="title">彩食鲜入库验收单</h2>
                <img id={`key${o.id}`} style={{position: 'absolute', top: '5px', left: '30px'}} />
                <div style={{position: 'absolute', top: '10px', right: '10px', fontSize: '12px'}}>
                  <p style={{margin: '0px'}}>打印日期：{o.printDate}</p>
                  <p style={{margin: '0px'}}>打印次数：{o.printTimes}</p>
                </div>
              </div>
              <div className='header'>
                <Row type="flex" justify="start">
                  {
                    id && id == 1 ? '' : <Col span={8} className='delBox'>
                      <label>入库批次：</label>
                      <span>{o.batchCode}</span>
                    </Col>
                  }
                  <Col span={8} className='delBox'>
                    <label>入库单：</label>
                    <span>{o.orderCode}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>来源单：</label>
                    <span>{o.originOrderCode}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>配送地点：</label>
                    <span>{`${o.sendLocationCode}-${o.sendLocationName}`}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>业务类型：</label>
                    <span>{o.businessType}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>入库时间：</label>
                    <span>{o.receiveTime}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>供应商：</label>
                    <span>{`${o.supplierCode}-${o.supplierName}`}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>入库地点：</label>
                    <span>{`${o.receiveLocationCode}-${o.receiveLocationName}`}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>总金额（元）：</label>
                    <span>{o.productAmount}</span>
                  </Col>
                </Row>
              </div>
              <div className='foot' style={{marginBottom: '10px'}}>
                <Table
                  rowKey={record => record.id}
                  loading={tableLoading}
                  columns={this.columns}
                  dataSource={o.productItems}
                  pagination={false}
                  rowKey={record => record.itemNo}
                  style={{marginTop: '6px'}}
                  bordered
                  size="middle"
                />
              </div>
              <div style={{textAlign: 'right'}}>
                <span style={{marginRight: '250px'}}>收货人：</span>
                <span style={{marginRight: '200px'}}>供应商签字：</span>
              </div>
            </section>)
        }
        <div className="printLoadingSty">
          <Spin spinning={printLoading} size="large" />
        </div>
      </React.Fragment>
    )
  }
}



